import React from 'react';
import {Row,Col} from 'react-bootstrap'
import CarouselProjet from '../Projet/CarouselProjet';

const Line2 = (props) => {

         
    return (
        <React.Fragment>
            <Row className="text-center" style={{ width:props.styleBoard.Board }}>
                <Col id="rook" className="dark text-light cursorPointer"
                 style={{width:props.styleBoard.Square,height:props.styleBoard.Square}}
                    data-toggle="collapse" data-target="#collapseRook"
                    aria-expanded="true" aria-controls="collapseRook">  
                    <div className="icomoonregular h-100 d-flex align-items-center justify-content-center" 
                    style={{ fontSize:props.styleBoard.tailleIcone }}>
                        r
                    </div>                           
                </Col>      
                <Col className="light text-secondary"  
                style={{width:props.styleBoard.Square,height:props.styleBoard.Square}}>
                    <div className="h-100 d-flex align-items-center justify-content-center"
                    style={{ fontSize:props.styleBoard.taillePolice }}>
                        Projets
                    </div>
                </Col>
                <Col className="dark text-secondary">.</Col>
                <Col className="light text-light">.</Col>
                <Col className="dark text-secondary">.</Col>
                <Col className="light text-light">.</Col>
                <Col className="dark text-secondary">.</Col>
                <Col className="light text-light">.</Col>                
            </Row>
           
            <Row style={{ width:props.styleBoard.Board }}>
                <div id="collapseRook" className="collapse container_collapse_Rook" aria-labelledby="rookButton" data-parent="#board">
                        <CarouselProjet/>
                </div>
            </Row>
            
        </React.Fragment>
    );
}

export default Line2;
