import React from 'react';
import {Row,Col} from 'react-bootstrap'

const Line7 = (props) => {
  
    return (
        <React.Fragment>
            <Row style={{ width:props.styleBoard.Board }}>
                <Col  style={{width:props.styleBoard.Square,height:props.styleBoard.Square}}
                className="light text-light">.</Col>
                <Col className="dark text-secondary">.</Col>
                <Col className="light text-light">.</Col>
                <Col className="dark text-secondary">.</Col>
                <Col className="light text-light">.</Col>
                <Col className="dark text-secondary">.</Col>
                <Col className="light text-light">.</Col>
                <Col className="dark text-secondary">.</Col>
            </Row>
            
        </React.Fragment>
    );
}

export default Line7;