import React, {useState} from 'react';
import {ButtonGroup, ToggleButton} from 'react-bootstrap'

const Toogle = (props2) => { 
    
    const [radioValue, setRadioValue] = useState('1');

    const toogleTheme = (e) =>{
        setRadioValue(e.currentTarget.value)
        props2.modifyTheme()
    }

    const radios =[
        {name :'Normal', value:'1'},
        {name :'Echecs', value:'2'}
    ]
   
    return (
        <React.Fragment>
            <ButtonGroup>
                {radios.map((radio, idx) => (
                <ToggleButton
                    key={idx}
                    id={`radio-${idx}`}
                    type="radio"
                    variant={idx % 2 ? 'outline-info' : 'outline-info'}
                    name="radio"
                    value={radio.value}
                    checked={radioValue === radio.value}
                    onChange={(e)=>toogleTheme(e)}
                >
                    {radio.name}
                </ToggleButton>
                ))}
            </ButtonGroup>        
        </React.Fragment>
    );
}

export default Toogle;