import React from 'react';
import {Row,Col} from 'react-bootstrap'
import clickKnight from '../Board/onClickKnight'

const Line6 = (props) => {

    /*clic sur les éléments dans la zone contact*/
    const clickMailButton = () =>{
        window.alert("sebastiendelaunay44@gmail.com")
    }

    const clickLinkedin =() =>{
        window.location.href="https://www.linkedin.com/in/sebastien-delaunay-5093151a3/"
    }

    const clickGithub =() =>{
        window.location.href="https://github.com/DelaunayS"
    }
      
    return (
        <React.Fragment>
            <Row className="text-center" style={{ width:props.styleBoard.Board }}>
                <Col id="knight" onClick={clickKnight}
                    className="icomoonregular dark text-light cursorPointer"
                    style={{width:props.styleBoard.Square,height:props.styleBoard.Square}}>
                    <span className="icomoonregular h-100 d-flex align-items-center justify-content-center"
                       style={{ fontSize:props.styleBoard.tailleIcone }}>
                          n
                    </span>               
                </Col>
                <Col className="light text-secondary"
                    style={{width:props.styleBoard.Square,height:props.styleBoard.Square}}>
                    <div className="h-100 d-flex align-items-center justify-content-center"
                    style={{ fontSize:props.styleBoard.taillePolice }}>
                        Contact
                    </div>
                </Col>
                <Col className="dark text-secondary">.</Col>
                <Col className="light text-light"
                    style={{width:props.styleBoard.Square,height:props.styleBoard.Square}}>
                    <div id="mailButton2" onClick={clickMailButton} 
                        style={{ fontSize:props.styleBoard.tailleIcone }}
                        className="icomoonregular h-100 align-items-center justify-content-center  text-danger cursorPointer">
                        I</div>
                </Col>
                <Col className="dark text-secondary">.</Col>
                <Col className="light text-light"
                    style={{width:props.styleBoard.Square,height:props.styleBoard.Square}}>
                    <div id="linkedin2" onClick= {clickLinkedin}
                        style={{ fontSize:props.styleBoard.tailleIcone }}
                        className="icomoonregular h-100 align-items-center justify-content-center  text-primary cursorPointer">
                        K
                    </div> 
                </Col>
                <Col className="dark text-secondary">.</Col>
                <Col className="light text-light"
                    style={{width:props.styleBoard.Square,height:props.styleBoard.Square}}>
                    <div id="github2" onClick= {clickGithub}
                        style={{ fontSize:props.styleBoard.tailleIcone }}
                        className="icomoonregular h-100 align-items-center justify-content-center text-dark cursorPointer">
                        J</div>
                </Col>     
            </Row>            
        </React.Fragment>
    );
}

export default Line6;