import React from 'react';
import {Row,Col} from 'react-bootstrap'

const Line1 = (props) => {
    
    return (
        <React.Fragment>
            <Row className="text-center" style={{ width:props.styleBoard.Board }} >
                <Col id="king" className="light text-secondary cursorPointer"
                   style={{width:props.styleBoard.Square,height:props.styleBoard.Square}}
                    data-toggle="collapse" data-target="#collapseKing"
                    aria-expanded="true" aria-controls="collapseKing">                   
                    <div className="icomoonregular h-100 d-flex align-items-center justify-content-center" 
                        style={{ fontSize:props.styleBoard.tailleIcone }}>
                        k
                    </div>                         
                </Col>
                <Col className="dark text-light"
                    style={{width:props.styleBoard.Square,height:props.styleBoard.Square}}>
                    <div className="h-100 d-flex align-items-center justify-content-center"
                        style={{ fontSize:props.styleBoard.taillePolice }}>
                        Accueil
                    </div>
                </Col>
                <Col className="light"></Col>
                <Col className="dark "></Col>
                <Col className="light"></Col>  
                <Col className="dark"></Col>    
                <Col className="light"></Col>               
                <Col className="dark"></Col>  
            </Row>     

            <Row style={{width:`${props.dimensionBoard}px`}}>
                <div id="collapseKing" className="collapse text-light" aria-labelledby="bishopButton" data-parent="#board">
                    <p className="text-center text-warning">Bienvenue !</p>
                    <p className="text-center"> Je vous invite à voyager sur toutes les cases de cet échiquier !! </p> 
                </div>
            </Row>

        </React.Fragment>
    );
}

export default Line1;