import React from 'react';
import {Row,Col} from 'react-bootstrap'
import Parties from '../Parties'

const Line5 = (props) => {
       
    return (
        <React.Fragment>
            <Row className="text-center" style={{ width:props.styleBoard.Board }}>
                <Col id="pawn" className="light text-secondary cursorPointer"
                style={{width:props.styleBoard.Square,height:props.styleBoard.Square}}
                     data-toggle="collapse" data-target="#collapsePawn"
                     aria-expanded="true" aria-controls="collapsePawn">
                     <span className="icomoonregular h-100 d-flex align-items-center justify-content-center"
                      style={{ fontSize:props.styleBoard.tailleIcone }}>
                         p
                    </span>
                </Col>
                <Col className="dark text-light"
               style={{width:props.styleBoard.Square,height:props.styleBoard.Square}}>
                    <div className="h-100 d-flex align-items-center justify-content-center"
                    style={{ fontSize:props.styleBoard.taillePolice }}>
                        Parties
                    </div>
                </Col>
                <Col className="light text-light">.</Col>
                <Col className="dark text-secondary">.</Col>
                <Col className="light text-light">.</Col>
                <Col className="dark text-secondary">.</Col>
                <Col className="light text-light">.</Col>
                <Col className="dark text-secondary">.</Col>
            </Row> 

             <Row style={{ width:props.styleBoard.Board }}>
                <div id="collapsePawn" className="collapse text-light" aria-labelledby="pawnButton" data-parent="#board">
                        <Parties/>
                </div>
            </Row>             
        </React.Fragment>
    );
}

export default Line5;