import React from 'react';
import {Container} from 'react-bootstrap'
import useWindowDimensions from './useWindowDimensions';
import '../Board/board.scss'

import Line1 from './line1'
import Line2 from './line2';
import Line3 from './line3';
import Line4 from './line4'
import Line5 from './line5';
import Line6 from './line6';
import Line7 from './line7';
import Line8 from './line8';

const Board = () => {
    /* gestion de la taille des cases et de l'échiquier*/
    const { heightWindow, widthWindow } = useWindowDimensions()

    let dimensionBoard

    if (heightWindow>widthWindow) {
        dimensionBoard=Math.round(widthWindow*0.8/8)*8                    
    }else{
        dimensionBoard=Math.round(heightWindow*0.8/8)*8   
    } 

    let dimensionSquare=dimensionBoard/8
    
    let taillePolice= `${Math.round(dimensionBoard/45)}px`   
     
    let taillePoliceIcone= `${Math.round(dimensionBoard/15)}px`

     /*définit les styles css de l'échiquier*/
    const styleBoard={  
        Board:`${dimensionBoard}px`, 
        Square:`${dimensionSquare}px`,        
        taillePolice:`${taillePolice}`,
        tailleIcone:`${taillePoliceIcone}`
    }       
    
    return (
        <Container fluid id="board" style={{ height:styleBoard.Board , width:styleBoard.Board }}>
            <Line1 styleBoard={styleBoard} />
            <Line2 styleBoard={styleBoard} />
            <Line3 styleBoard={styleBoard} />
            <Line4 styleBoard={styleBoard}/>
            <Line5 styleBoard={styleBoard}/>
            <Line6 styleBoard={styleBoard}/>
            <Line7 styleBoard={styleBoard}/>
            <Line8 styleBoard={styleBoard}/>
        </Container>
    );
}

export default Board;