import React from 'react';

import '../Footer/footer.scss'


/*clic sur les éléments dans la zone contact*/
const clickMailButton = () =>{
    window.alert("sebastiendelaunay44@gmail.com")
}

const clickLinkedin =() =>{
    window.location.href="https://www.linkedin.com/in/sebastien-delaunay-5093151a3/"
}

const clickGithub =() =>{
    window.location.href="https://github.com/DelaunayS"
}

const Footer = () => {
    return (
        <div className="Footer container text-light">
           <div id="contact" className='row'>
               <div className='col-4'>
                   © Sébastien Delaunay 
               </div>
               <div className='col-2 text-center'>
                   <span  id="mailButton" onClick={clickMailButton} className="text-danger bg-light">I</span>
               </div>
               <div className='col-2 text-center '>
                   <span id="linkedIn" onClick={clickLinkedin} className="text-primary bg-light">K</span>
               </div>               
               <div className='col-2 text-center'>
                   <span id="github" onClick={clickGithub}>J</span>
               </div>
               <div className='col-2 text-end'>
                   @2021
               </div>
           </div>
        </div>
    );
}

export default Footer;
